
.mastFooter {
    position: relative;
    text-align: left;
    color: #abacb1;
    overflow: hidden;
    background: url("../../assets/images/bg_parallax_05.jpg") no-repeat 50% 50% / cover fixed;
}
.mastFooter .inner {
    padding: 60px 0;
    background: rgba(11, 25, 45, 0.85);
}
.mastFooter .inner .button:hover {
    background: #fff;
    color: #ffa726;
}
.mastFooter .logo {
    margin-bottom: 30px;
    position: relative;
    top: -8px;
}
.mastFooter .logo img{
    width: 100px;
}
.mastFooter .copyright {
    font-size: 12px;
    margin-bottom: 0;
}
.mastFooter a {
    color: #abacb1;
}
.mastFooter a:hover {
    color: #ffa726;
}
.mastFooter h6 {
    color: #fff;
    margin: 0 0 40px 0;
    font-weight: 300;
}
.mastFooter .social,
.mastFooter .links,
.mastFooter .contact {
    margin: 0;
}
.mastFooter .social a {
    color: #ffa726;
}
.mastFooter .social a:hover {
    color: #fff;
}
.mastFooter .social li {
    display: inline-block;
    margin-right: 10px;
}
.mastFooter .links li,
.mastFooter .contact li {
    margin-bottom: 6px;
}
.mastFooter .contact li {
    padding-left: 35px;
    position: relative;
}
.mastFooter .contact i {
    position: absolute;
    left: 0;
    top: 2px;
}
.mastFooter .banner {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    padding-bottom: 45px;
    margin-bottom: 50px;
    color: #fff;
}

@media only screen and (max-width: 767px) {
    .mastFooter h6 {
        margin: 20px 0 10px 0;
    }
}


