@media (min-width: 992px) {
    .navList {
        text-align: right;
        margin-bottom: 0;
        z-index: 999;
        font-family: "Montserrat", sans-serif;
        font-size: 0.9em;
        font-weight: 300;
    }

    .navList li {
        display: inline-block;
        position: relative;
        height: 70px;
        margin-left: 50px;
    }

    .navList li a {
        color: #fff;
        text-transform: uppercase;
        display: block;
        height: 70px;
        line-height: 70px;
        position: relative;
    }

    .navList li a:hover {
        color: #ffa726;
    }

    .navList li a.selected::before {
        background: #ffa726;
        content: "";
        width: 3px;
        height: 10px;
        position: absolute;
        top: 50%;
        margin-top: -5px;
        left: -12px;
    }

    .navList li i.fa {
        color: rgba(255, 255, 255, 0.9);
        font-size: 8px;
        position: relative;
        left: 4px;
    }

    .navList li:hover {
        visibility: inherit;
    }

    .navList ul {
        line-height: normal;
        min-width: 180px;
        text-align: left;
        font-size: 13px;
        position: absolute;
        top: 120%;
        padding: 10px 0;
        background: rgba(11, 25, 45, 0.93);
        left: 0;
        white-space: nowrap;

        opacity: 0;
        visibility: hidden;
        will-change: transform;
        -o-transition: all 0.3s ease-in-out;
        -webkit-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    .navList ul.align-left {
        left: -130px;
    }

    .navList ul.mega ul {
        position: static;
        background: none;
        padding: 0;
        margin: 0;
    }

    .navList ul.mega > li {
        float: none;
        width: 180px;
        vertical-align: top;
    }

    .navList ul.mega .title {
        padding: 10px 20px;
        text-transform: uppercase;
        display: inline-block;
        color: rgba(255, 255, 255, 0.6);
    }

    .navList ul li {
        padding: 0;
        margin: 0;
        height: auto;
        float: left;
        width: 180px;
    }

    .navList ul a {
        color: #fff;
        display: block;
        height: auto;
        line-height: normal;
        float: none;
        padding: 10px 20px;
        text-transform: none;
    }

    .navList ul li:last-child a {
        border: none;
    }

    .navList ul a:hover {
        color: #ffa726;
    }

    .navList li:hover > ul.dropMenu {
        top: 100%;
        z-index: 99;
        opacity: 1;
        visibility: visible;
    }

    .navList li:hover > ul ul {
        opacity: 1;
        visibility: visible;
    }
}

@media (max-width: 991px) {
    .navList {
        z-index: 1002;
        position: absolute;
        background: rgba(11, 25, 45, 0.93);
        padding: 10px 0;
        margin-bottom: 0;
        top: 0;
        right: 0;
        left: 0;
        display: block;

        opacity: 0;
        visibility: hidden;
        will-change: transform;
        -o-transition: all 0.3s ease-in-out;
        -webkit-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    .navList.open {
        opacity: 1;
        visibility: visible;
    }

    .navList ul.dropMenu {
        margin: 0;
        padding: 0 0 10px 20px;
        border-bottom: 1px solid #434462;
        display: none;
    }

    .navList ul.dropMenu.open {
        display: block;
    }


    .navList a {
        display: block;
        padding: 5px 20px;
        color: #fff;
    }

    .navList ul span.title {
        text-transform: uppercase;
        display: inline-block;
        color: rgba(255, 255, 255, 0.6);
    }

    .navList i.fa {
        color: #696981;
        font-size: 12px;
        margin-left: 5px;
    }

    .navList a:focus {
        color: #fff;
    }

    .navList a.selected,
    .navList a:hover {
        color: #ffa726;
    }
}


