.companyInfo {
    padding: 30px;
    pointer-events: auto;
    background: #ffc960;
}
.companyInfo ul {
    margin-top: 50px;
}
.companyInfo li {
    position: relative;
    padding-left: 40px;
    margin-bottom: 20px;
    color: #fff;
}
.companyInfo li a {
    color: #fff;
}
.companyInfo h5 {
    color: inherit;
}
.companyInfo li a:hover {
    color: #fff;
}
.companyInfo li i {
    color: #fff;
    position: absolute;
    left: 0;
    top: 4px;
    font-size: 20px;
}


