    /*-webkit-transform-style: preserve-3d;*/
    /*-webkit-perspective: initial;*/
}

.slick-slide.slick-active .item__description {
    /*-webkit-transform: translateY(0%);*/
    /*-moz-transform: translateY(0%);*/
    /*-ms-transform: translateY(0%);*/
