.item__description__title {

}

.item__description__title {
    color: #fff;
}

.item__description__title .break {
    display: block;
}

.item__description__title h1 {
    font-weight: 600;
    margin-bottom: 80px;
    font-size: 50px;
    color: #fff;
}

.item__description__title p {
    margin: 0 0 5px 0;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    font-size: 0.9em;
    text-transform: uppercase;
}

.item__description__buttons .button.transparent {
    background: rgba(255, 255, 255, 0.2);
    outline: 1px solid rgba(255, 255, 255, 0.4);
    color: #fff;
}

.item__description__buttons .button.transparent:hover {
    background: #0b192d;
    outline: 1px solid #0b192d;
    color: #fff;
}

/*dots*/
.slick-dots {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 25px;
    position: absolute;
    top: 48%;
    right: 0;
}

.slick-dots li {
    display: block;
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: 0;
    background: 0 0;
}

.slick-dots li.slick-active button {
    pointer-events: none;
}

.slick-dots li button:before {
    position: absolute;
    width: 5px;
    height: 5px;
    opacity: 1;
    content: "";
    border-radius: 50%;
    border: 2px solid white;
}

.slick-dots li.slick-active button:before {
    opacity: 1;
    background: white;
}

/*Responsive*/
@media only screen and (max-width: 767px) {
    .item__description__title h1 {
        font-size: 2.5em;
    }
}
@media only screen and (max-width: 540px) {
    .item__image {
        max-height: 500px;
    }

    .slick-slide .item__description {
        top: 35%;
    }

    .item__description__title h1 {
        font-size: 1.6em;
    }

    .item__description__buttons a {
        padding: 6px 12px !important;
    }
}


